import React from 'react';

import styles from './bounties.module.css';

const Bounties = () => {
  return(
    <div className={styles.container}>
      <h2 className={styles.subheading}>BOUNTIES</h2>
      <p className={styles.paragraph}>
        We offer various bounties for finding errors, video production, referring developers and more. Current bounties can be found here.
      </p>
      <button className={styles.button}>BOUNTIES</button>
      <p className={styles.paragraph}>
        To stay up-to-date with the Shardus project, subscribe to our newsletter!
      </p>
      <input className={styles.input} type="text" placeholder="Your Email"/>
      <div className={styles.noRobot}>I'm not a robot</div>
    </div>
  );
};

export default Bounties;
